body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.ripple {
  text-align: center;
  display: inline-block;
  padding: 8px 30px;
  letter-spacing: .5px;
  border-radius: 2px;
  text-decoration: none;
  color: #fff;
  overflow: hidden;
  position: relative;
  font-weight: 700;
  transition: all 0.4s ease;
}

.ink {
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 100%;
  -webkit-transform: scale(0);
  -moz-transform: scale(0);
  -o-transform: scale(0);
  transform: scale(0);
}

.animate {
  animation: ripple 0.9s linear;
}

@-webkit-keyframes ripple {
  100% {
    opacity: 0;
    -webkit-transform: scale(2.5);
  }
}

@-moz-keyframes ripple {
  100% {
    opacity: 0;
    -moz-transform: scale(2.5);
  }
}

@-o-keyframes ripple {
  100% {
    opacity: 0;
    -o-transform: scale(2.5);
  }
}

@keyframes ripple {
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}

.blue {
  background-color: #2196F3;
}

